<template>
    <div>
        <!-- Navigation Bar Component -->
        <NavBar></NavBar>

        <div class="inner-block-custom">
            <p>
                <label>Balance: <b>{{ this.userBalance }}</b></label>
            </p>

            <div class="p-3" style="width: auto; margin: 20px 10px;">
                <!-- Overlay Component -->
                <OverlayComponent :show="showOverlay">
                    <div>
                        <b-tabs content-class="mt-3" fill>
                            <div>
                                <!-- Modal Dialog Component -->
                                <ModalDialog v-model="isModalOpen" :text="modalText" />
                            </div>
                            <b-tab title="Operations" active>
                                <!-- Operation Display Component -->
                                <OperationDisplay :num1="num1" :num2="num2" :operationValue="operationValue" />

                                <!-- Operation Buttons Component -->
                                <OperationButtons :buttons="buttons" @operator-click="handleOperatorClick" />

                                <div style="align-items: center; text-align: center; margin: 0 130px 0 130px; padding: 3%;">
                                    <!-- Result Display Component -->
                                    <b-alert show :variant="variantAlert" style="text-align: center; margin-bottom: 0px;">
                                        <h1 style="font-size: 1.2vw">{{ this.result }}</h1>
                                    </b-alert>
                                </div>

                                <div style="text-align: center; padding: 3%;">
                                    <!-- Overlay Component for Buttons -->
                                    <OverlayComponent :show="showOverlayBtn">
                                        <b-button-group>
                                            <b-button variant="success" :disabled="busy" size="lg" @click="calculate()">
                                                Calculate
                                            </b-button>
                                            <b-button variant="primary" :class="{ 'disabled': !this.isArithmetic }"
                                                @click="generateRandomNums()">
                                                Random Numbers
                                            </b-button>
                                        </b-button-group>
                                    </OverlayComponent>
                                </div>
                            </b-tab>
                            <b-tab title="Records">
                                <!-- Records Table Component -->
                                <RecordsTable />
                            </b-tab>
                        </b-tabs>
                    </div>
                </OverlayComponent>
            </div>
        </div>
    </div>
</template>

<script>
import logic from "@/assets/js/logic";
import ModalDialog from "@/components/ModalDialog";
import OperationDisplay from "@/components/OperationDisplay";
import OperationButtons from "@/components/OperationButtons";
import ResultDisplay from "@/components/ResultDisplay";
import RecordsTable from "@/components/RecordsTable";
import OverlayComponent from "@/components/Overlay.vue";
import NavBar from "@/components/NavBar";

export default {
    components: {
        ModalDialog,
        OperationDisplay,
        OperationButtons,
        ResultDisplay,
        RecordsTable,
        OverlayComponent,
        NavBar,
    },
    computed: {
        loggedIn() {
            return localStorage.getItem("loggedIn") === "true";
        },
        getUserName() {
            return localStorage.getItem("userName");
        },
        getUserBalance() {
            return localStorage.getItem("userBalance");
        },
    },
    created() {
        if (!this.loggedIn) {
            this.$router.push("/login");
        }
        this.userName = this.getUserName;
        this.userBalance = this.getUserBalance;
        this.fetchData();
    },
    data() {
        return {
            busy: false,
            showOverlay: false,
            showOverlayBtn: false,
            buttons: [],
            num1: null,
            num2: null,
            isModalOpen: false,
            modalText: "",
            operationValue: "",
            result: "Select an operation type.",
            isArithmetic: true,
            variantAlert: "info",
            userName: "",
            userBalance: "",
        };
    },
    methods: {
        async fetchData() {
            // Show overlays during data fetching
            this.showOverlay = !this.showOverlay;
            this.showOverlayBtn = !this.showOverlayBtn;

            await Promise.all([this.generateRandomNums()]);
            this.fetchButtons();

            // Hide overlays after data fetching
            this.showOverlayBtn = !this.showOverlayBtn;
            this.showOverlay = !this.showOverlay;
        },
        async fetchButtons() {
            try {
                const response = await logic.getAvOperations();
                this.buttons = response.data.data;
            } catch (error) {
                this.openModal(error.message);
            }
        },
        handleOperatorClick(operator, opId, isArithmetic) {
            if (!isArithmetic) {
                this.num1 = null;
                this.num2 = null;
            } else if (operator === "√") {
                this.num2 = null;
            }
            this.operationValue = operator;
            this.operationId = opId;
            this.isArithmetic = isArithmetic;
        },
        async generateRandomNums() {
            // Show overlay for generating random numbers
            this.showOverlayBtn = !this.showOverlayBtn;

            try {
                const response = await logic.getRandomNumbers("numeric=true");
                const [num1, num2] = response.data.data;

                if (this.operationValue === "√") {
                    this.num1 = num1;
                    this.num2 = null;
                } else {
                    this.num1 = num1;
                    this.num2 = num2;
                }
            } catch (error) {
                this.openModal(error.message);
            }

            // Hide overlay for generating random numbers
            this.showOverlayBtn = !this.showOverlayBtn;
        },
        async calculate() {
            if ((this.num1 || this.num2) === null && this.isArithmetic) {
                this.resultDisplayHandler(
                    "Please generate random value(s) to proceed with the operation.",
                    "warning"
                );
            } else if (this.operationValue === "") {
                this.resultDisplayHandler("Please select a valid operation.", "warning");
            } else {
                // Show overlay during calculation
                this.showOverlayBtn = !this.showOverlayBtn;

                try {
                    let result;
                    if (!this.isArithmetic) {
                        const response = await logic.getRandomNumbers("numeric=false");
                        this.string = response.data.data[0];
                        result = await logic.OperationCalculation(
                            this.string,
                            this.num2,
                            this.operationId
                        );
                    } else {
                        result = await logic.OperationCalculation(
                            this.num1,
                            this.num2,
                            this.operationId
                        );
                    }

                    this.userBalance = this.result.data.data["user_balance"];
                    this.result = this.result.data.data["operation_response"];
                    this.resultDisplayHandler(this.result, "success");
                } catch (error) {
                    this.openModal(error.response.data.message);
                }

                // Hide overlay after calculation
                this.showOverlayBtn = !this.showOverlayBtn;
            }
        },
        openModal(msg) {
            this.isModalOpen = true;
            this.modalText = msg;
        },
        resultDisplayHandler(msg, variant) {
            this.result = msg;
            this.variantAlert = variant;
        },
    },
};
</script>
